.Item {
    display:flex;
    flex-direction:column;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    margin: 10px;
    padding: 20px;
    min-width: 200px;
    max-height: 400px;
    background-color: white;
    z-index:1;


}
.Item > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;

}
.Item__info {
    height: 100px;
    margin-bottom: 15px;
    font-size: small;
}
.Item__price {
    margin-top: 5px;
}
.Item > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;


}
.Item__rating {
    display: flex;
}