.home {
    
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
    

    
}
.home__row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;

}
.home__image{
    
    width:100%;
    z-index:-1;
    margin-bottom:-150px;
    mask-image: linear-gradient(to top, transparent 10%, black 60%);

}